title {
  color: white;
  padding: 5px 20px;
  font-size: 1.5rem;
  text-align: left;
  background-color: var(--dark-pink);
}

.continents {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 150px 150px 150px;
}

.continents div {
  display: inline-grid;
  grid-template-columns: 50% 50%;
  position: relative;
}

.continents div:nth-child(2),
.continents div:nth-child(3),
.continents div:nth-child(6) {
  background-color: var(--dark-pink);
}

.mapDiv {
  text-align: center;
  height: 25vh;
  max-height: 25vh;
  margin: 10px 0 20px 0;
}

.map {
  max-width: 100%;
  width: 100%;
  height: 100%;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px 0;
}

.navbar__img {
  width: 30px;
  height: auto;
}

.navbar__title {
  color: white;
  font-weight: 200;
}

.filter-section {
  display: flex;
  padding: 5px;
}

.stats {
  width: 20%;
  padding: 10px;
}

.input-group {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  width: 100%;
  margin-right: 5vw;
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  appearance: none;
  border-radius: 0.25rem;
}
